.offers .carousel-dark .carousel-indicators [data-bs-target] {
  background-color: #FF9846;
}
.offers .carousel:first-child {
  margin: 30px 0 0 0;
}
.offers .carousel:first-child .carousel-control-next {
  display: none;
}
.offers .carousel:first-child .carousel-control-prev {
  display: none;
}
.offers .carousel:first-child .carousel-indicators {
  transform: translate(0, 170%);
  display: flex;
  justify-content: center;
  align-items: center;
}
.offers .carousel:first-child .carousel-indicators button {
  width: 8px;
  height: 8px;
  border-radius: 100%;
  margin: 0 6px;
}
.offers .carousel:first-child .carousel-indicators .active {
  width: 12px;
  height: 12px;
}
.offers .carousel:first-child .carousel-item {
  position: relative;
  width: 920px;
  height: 517.5px;
  background-color: #FFFFFF;
}
.offers .carousel:first-child .carousel-item .carousel-item-img {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 82.61%;
}
.offers .carousel:first-child .carousel-item .offers__product {
  position: absolute;
  top: 0;
  right: 0;
}
.offers .carousel:last-child {
  display: flex;
  align-items: center;
}
.offers .carousel:last-child .carousel-control-next {
  height: 30%;
  transform: translate(0, 115%);
}
.offers .carousel:last-child .carousel-control-next span {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 60px;
  height: 60px;
  border-radius: 100%;
  background: #111111;
}
.offers .carousel:last-child .carousel-control-next span img {
  transform: translate(10%, 0);
}
.offers .carousel:last-child .carousel-control-prev {
  transform: translate(0, 115%);
  height: 30%;
}
.offers .carousel:last-child .carousel-control-prev span {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 60px;
  height: 60px;
  border-radius: 100%;
  background: #111111;
}
.offers .carousel:last-child .carousel-control-prev span img {
  transform: translate(-10%, 0);
}
.offers__product {
  background-color: transparent;
  z-index: 0;
  padding: 0 20px 0 0;
  height: 517px;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.offers__productImg {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 90%;
  z-index: 0;
}
.offers__productInfo {
  z-index: 1;
  padding: 30px 0 0 0;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  align-self: flex-end;
  font-family: Din Pro;
}
.offers__productInfo .offers__productName {
  font-size: 2rem;
  font-weight: 700;
  padding: 0 0 5px 0;
}
.offers__productPrice {
  z-index: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  align-self: flex-end;
  font-family: Din Pro;
  font-weight: 700;
}
.offers__productPrice .offers__productPriceOldPrice {
  z-index: 0;
  font-size: 1.3rem;
  margin: 0 0 15px 0;
}
.offers__productPrice .offers__productPriceCurrentPrice {
  font-size: 2rem;
}
.offers__productBtn {
  z-index: 1;
  align-self: flex-end;
  font-family: Din Pro;
  color: #FFFFFF;
  font-size: 1.75rem;
  padding: 15px 95px;
  margin: 0 10px 20px 0;
  border-radius: 4px;
  background-color: #F46D3F;
}
.offers__productBtn:hover {
  color: #F46D3F;
  background-color: #FFFFFF;
}
.offers__sliderValue {
  padding: 20px 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.offers__sliderValue circle:hover {
  transition: 0.15s;
  fill: #FF9846;
}
.offers .catalog {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 100px 0 20px 0;
}
.offers .catalog__content {
  width: 100%;
  position: relative;
  display: grid;
  grid-gap: 10px;
  grid-template-areas: "one one two three" "four four five five";
}
.offers .catalog__content-item {
  display: flex;
  align-items: flex-end;
  position: relative;
  min-height: 200px;
}
.offers .catalog__content-item img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
.offers .catalog__content-item p {
  position: absolute;
  bottom: 0;
  left: 0;
  font-family: Din Pro;
  font-weight: 500;
  font-size: 1.5rem;
  line-height: 1.86125rem;
  z-index: 1;
  color: #FFFFFF;
  padding: 0 0 10px 10px;
}
.offers .catalog__content-item:hover p {
  transition: 0.3s;
  font-size: 1.875rem;
}
.offers .catalog__content-item:nth-child(1) {
  grid-area: one;
}
.offers .catalog__content-item:nth-child(2) {
  grid-area: two;
}
.offers .catalog__content-item:nth-child(3) {
  grid-area: three;
}
.offers .catalog__content-item:nth-child(4) {
  grid-area: four;
}
.offers .catalog__content-item:nth-child(5) {
  grid-area: five;
}
.offers .novelty {
