@media screen and (min-width: 1870px) {
  .offers .slide .carousel-control-prev {
    transform: translate(-90%, 115%) !important;
    height: 30%;
  }
  .offers .slide .carousel-control-next {
    transform: translate(90%, 115%) !important;
    height: 30%;
  }
}